.bodyX{
    margin: 0;
    padding: 0;
    font-family: 'Josefin Sans',sans-serif;
    -webkit-align-self: center;
            align-self: center;
    height: 25%;
}
.textX{
    padding-top: 30%;
    color:black;
    font-size: 20px;
}
.cardX{
    display: inline-block;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-perspective: 600px;
            perspective: 600px;
    transition: 0.4s; 
}

@media screen and (max-width:800px){
    .myImage{
        width: 25vw; 
    }
    .textX{
        padding-top: 30%;
        color:black;
        font-size: 3vw;
    }
}


.cardX .frontX{
    top: 0;
    overflow:hidden;
    width: 100%;
    height: 100%;
    
    background: #000;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
    transition: 0.2s;
}
.cardX:hover .frontX{
    -webkit-transform: rotateX(-180deg);
            transform: rotateX(-180deg);
}
.cardX .backX{
    position: absolute;
    overflow:hidden;
    top: 0;
    width: 100%;
    height: 100%;
    
    /* background: red; */
    /* golden card */
    background-image: url("https://hmp.me/cjez");
    /* black card */
    /* background-image: url("https://hmp.me/cje2"); */
    background-size: 100% 100%;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -webkit-transform: rotateX(180deg);
            transform: rotateX(180deg);
    transition: 0.2s;
}
.cardX:hover .backX{
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
}

